@font-face {
  font-family: 'Nunito Sans';
  src: local('Nunito Sans'), url(./assets/font/nunitosans-regular.ttf) format('truetype');
}

body {
  margin: 0;
  /* font-family: "Poppins", sans-serif; */
  font-family: 'Nunito Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

ul li {
  color: #15499E;
}

h3 {
  font-size: calc(1vw + 1em);
}

h3.head-label {
  font-weight: 600;
  /* color: #171717; */
  color: #15499E;
  letter-spacing: .8px;
}

.label-underline {
  display: block;
  width: 2.5em;
  margin: 15px 0;
  border-bottom: 2px solid #ed367f;
}

h3.head-title {
  font-weight: 600;
  color: #15499e;
}