/* AntD CSS Overrides */
.ant-btn {
    background-color: #ed367f;
    background: #ed367f 0% 0% no-repeat padding-box;
    padding: 8px 2em;
    border: none;
    color: white;
    text-align: center;
    height: auto;
    border-radius: 2px;
}

.ant-modal .ant-modal-close {
    width: 25px;
    height: 25px;
}